import modalCheck from "../../assets/main/modal/modalCheck.svg";
import cross from "../../assets/main/modal/close.svg";
import styles from "./Modal.module.css";
import { useEffect } from "react";

export const Modal = ({ isActive, setIsActive }) => {
  useEffect(() => {
    const headerWrapper = document.getElementById("header-wrapper");

    if (isActive) {
      headerWrapper.style.position = "sticky";
    } else {
      headerWrapper.style.position = "static";
    }

    return () => {
      headerWrapper.style.position = "";
    };
  }, [isActive]);

  const handleCloseModal = () => {
    setIsActive(false);
  };

  return (
    <div className={styles.modal} onClick={handleCloseModal}>
      <div className={styles.modalContent} onClick={(e) => e.stopPropagation()}>
        <img
          src={cross}
          alt="close"
          className={styles.close}
          onClick={handleCloseModal}
        />
        <h2 className={styles.modalTitle}>ÐÑÐºÑÑÐ¼Ð¾!</h2>
        <p className={styles.message}>
          ÐÐ°ÑÐµ Ð¿Ð¾Ð²ÑÐ´Ð¾Ð¼Ð»ÐµÐ½Ð½Ñ Ð½Ð°Ð´ÑÑÐ»Ð°Ð½Ðµ. ÐÐ°Ñ Ð¼ÐµÐ½ÐµÐ´Ð¶ÐµÑ Ð·Ð²âÑÐ¶ÐµÑÑÑÑ Ð· Ð²Ð°Ð¼Ð¸ Ð½Ð°Ð¹Ð±Ð»Ð¸Ð¶ÑÐ¸Ð¼
          ÑÐ°ÑÐ¾Ð¼
        </p>
        <img className={styles.img} src={modalCheck} alt="completed" />
      </div>
    </div>
  );
};
